.imageGrid {
  width: 1728px;
  height: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}

.imageGridItem {
  position: relative;
}

.imageGridItem img {
  width: 100%;
  height: auto;
}

.imageGridItem img:hover {
  cursor: pointer;
  filter: brightness(0.4);
}

.imageGridItem .imageText {
  position: absolute;
  top: 10px;
  left: 10px;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}

.title {
  padding-left: 20px;
  font-size: 32px;
  display: block;
}

.price {
  font-size: 14px;
  padding-left: 20px;
  font-weight: 400;
}
