body {
  cursor: default;
}

.header {
  width: 100%;
}

.top {
  position: relative;
  width: 100%;
  height: 1024px;
}

.main-img {
  width: 100%;
  position: absolute;
}

.main-img img {
  width: 100%;
  height: 1024px;
}

.main-img span {
  position: absolute;
  font-size: 45px;
  color: #fff;
  bottom: 175px;
  right: 6.2vw;
  text-align: right;
  font-weight: bold;
}

.main-img p {
  position: absolute;
  bottom: 130px;
  right: 6.4vw;
  font-size: 16px;
  text-align: right;
}

.main-img p:hover {
  text-decoration: underline;
  cursor: pointer;
}

.header-container {
  position: absolute;
  padding: 20px 0 20px 0;
  width: 90vw;
  height: 85px;
  left: 96px;
  box-sizing: border-box;
}

.header-container .logo {
  margin: 0 auto;
  width: 117px;
  height: 53px;
}

.logo img {
  width: 117px;
  height: 53px;
}

.nav {
  position: absolute;
  left: 87.9vw;
  top: 18px;
}

.nav:visited {
  left: 60.9vw;
}

.menu {
  position: absolute;
  width: 368px;
  height: 1024px;
  top: -18px;
  right: -88px;
  background-color: #101010;
}

.menu .menu-text {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 388px;
  top: 256px;
}

.menu .text-in {
  line-height: 50px;
  color: #858585;
}

.menu .text-in:hover {
  color: #f7f7f7;
  border-left: 3px solid #f7f7f7;
}

.menu .logo-text-wh {
  position: absolute;
  width: 137px;
  height: 53px;
  left: 119px;
  bottom: 213px;
}

.menu-icon {
  position: absolute;
  bottom: 0;
  width: 368px;
  height: 41px;
}

.menu-icon .icon {
  float: left;
  width: 92px;
  height: 41px;
  border: 1px solid #858585;
  box-sizing: border-box;
  cursor: pointer;
}

.menu-icon img {
  width: 16px;
  height: 16px;
  padding: 12px 34.5px 0 34.5px;
}

.korea {
  position: absolute;
  bottom: 41px;
  float: left;
  width: 368px;
  height: 41px;
  border: 1px solid #858585;
  box-sizing: border-box;
}

.korea p {
  padding-top: 12px;
  font-size: 12px;
  float: left;
  color: #d1d1d1;
}

.korea img {
  float: left;
  width: 16px;
  height: 16px;
  padding: 12px 77.5px 0 34.5px;
}

.footer {
  margin: 0 auto;
  width: 100%;
  height: 300px;
}

.footer-top {
  border-top: 1px solid #4d4d4d;
  display: flex;
  justify-content: center;
}

.footer-top img {
  padding: 40px 0 40px 0;
  width: 193px;
  height: 77px;
}

.footer-middle {
  display: flex;
  justify-content: center;
  font-size: 14px;
}

.footer-middle p {
  padding-right: 100px;
}

.footer-middle p:last-child {
  padding-right: 0;
}

.footer-bottom {
  padding-top: 50px;
  display: flex;
  justify-content: center;
  text-align: center;
}

.footer-bottom {
  font-size: 12px;
}
