.service {
  width: 100%;
  height: 1628px;
  padding-top: 96px;
  position: relative;
}

.service img {
  width: 100%;
  height: 1152px;
}

.service .text_top {
  padding-top: 74px;
  padding-bottom: 54px;
  text-align: center;
}

.text_top h5 {
  font-size: 16px;
  font-weight: normal;
}

.text_top h4 {
  font-size: 22px;
  padding: 15px 0 15px 0;
  font-weight: normal;
}

.text_bottom {
  margin: auto;
  width: 1189px;
  height: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.text_bottom h6 {
  font-size: 20px;
  padding-bottom: 8px;
  font-weight: bold;
}

.text_bottom p {
  font-size: 14px;
  padding-bottom: 25px;
  color: #bdbdbd;
}

.text_bottom span {
  font-size: 14px;
  color: #bdbdbd;
}

.text_bottom span:hover {
  cursor: pointer;

  text-decoration: underline;
}

.form {
  position: absolute;
  width: 1172px;
  height: 1020px;
  background-color: aliceblue;
  top: 300px;
  z-index: 9000;
}
