.wrap {
  width: 100%;
  height: auto;
}

/* 2023 ESCALADE  */
.escalade {
  width: 100%;
  height: 459px;
}

.escalade_in {
  position: relative;
  width: 100%;
  height: 459px;
}

.escalade_in img {
  width: 100%;
  height: 459px;
}

.escalade_text {
  position: absolute;
  width: 90vw;
  height: 354px;
  top: 63px;
  left: 96px;
}

.escalade_text h3 {
  font-size: 32px;
  padding-bottom: 246px;
  font-weight: bold;
}

.escalade_text ul {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
}

.escalade_text li {
  font-size: 16px;
  float: left;
}

.escalade_text li:last-child {
  padding-top: 30px;
  font-size: 16px;
}

.escalade_text li:last-child:hover {
  text-decoration: underline;
  cursor: pointer;
}
