/* RESERVATION */
.reservation {
  background-attachment: fixed;
  position: relative;
  width: 100%;
  height: 1080px;
}

.reservation img {
  width: 100%;
  height: 1080px;
}

.reservation h3 {
  position: absolute;
  font-weight: normal;
  font-size: 37px;
  bottom: 161px;
  left: 97px;
}

.reservation h4 {
  position: absolute;
  bottom: 100px;
  left: 97px;
  font-size: 16px;
}

.reservation span {
  position: absolute;
  bottom: 100px;
  right: 6.2vw;
  font-size: 16px;
}

.reservation span:hover {
  cursor: pointer;
  text-decoration: underline;
}

.fm {
  position: absolute;
  width: 1157px;
  height: 988px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
}

.closeButton {
  background-color: transparent;
  position: absolute;
  top: 20px;
  right: 30px;
  color: #292929;
  border: none;
  padding: 5px 10px;
  font-size: 32px;
  cursor: pointer;
}

.list li:first-child {
  color: #000;
  text-align: center;
  font-size: 26px;
  padding: 30px 0 20px 0;
}

.list li:nth-child(2) {
  color: #000;
  font-size: 18px;
  text-align: center;
  padding-bottom: 30px;
  padding-left: 0;
}

.list li {
  padding-left: 90px;
  padding-top: 20px;
  color: #000;
  font-size: 18px;
}

.list li:nth-child(5) {
  padding-bottom: 20px;
}

input[type="checkbox"] {
  margin-right: 5px;
  width: 16px;
  height: 16px;
}

.fm hr {
  width: 970px;
  margin: 0 auto;
}

.carType {
  color: #000;
  margin: auto;
  width: 970px;
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
}

.carType li {
  float: left;
}

.carType li:hover {
  text-decoration: underline;
  cursor: pointer;
  font-weight: 800;
}

.carType li:first-child {
  width: 170px;
  padding-right: 30px;
}

.carType li:first-child:hover {
  text-decoration: none;
  cursor: default;
  font-weight: normal;
}

.area {
  color: #000;
  margin: auto;
  width: 970px;
  display: flex;
  justify-content: flex-start;
  padding: 30px 0;
}

.area p {
  width: 280px;
}

.area_inner {
}

.area input {
  color: rgb(177, 177, 177);
}

.area1,
.area2,
.area3 {
  padding: 5px 0;
  margin-right: 40px;
}

.area1 {
  width: 220px;
}

.area2 {
  width: 180px;
}

.area3 {
  width: 60px;
  text-align: center;
  cursor: pointer;
}

.way {
  color: #000;
  margin: auto;
  width: 970px;
  padding: 30px 0;
}

.way p {
  float: left;
  width: 280px;
  padding-bottom: 100px;
}

.way_checkboxBox {
  width: 560px;
  float: left;
  display: flex;
  justify-content: flex-start;
  border: 1px solid #6b6b6b;
  padding: 20px;
  margin-bottom: 10px;
  border-radius: 20px;
  line-height: 1.5;
}

.way_checkboxBox input[type="checkbox"] {
  margin: 3px 10px 0 0;
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.datePicker {
  color: #000;
  margin: auto;
  width: 970px;
  display: flex;
  justify-content: flex-start;
  padding: 30px 0;
}

.datePicker p,
.userInfo p {
  float: left;
  width: 280px;
}

.dateInput {
  padding: 5px 0;
  color: rgb(177, 177, 177);
}

.userInfo {
  color: #000;
  margin: auto;
  width: 970px;
  display: flex;
  justify-content: flex-start;
  padding: 30px 0;
}

.userInfo input[type="checkbox"] {
  margin: 3px 10px 0 0;
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.submitButton {
  padding: 40px 0;
  text-align: center;
}

.sub_Button {
  font-size: 18px;
  outline: none;
  padding: 10px 50px;
  background: #373737;
  color: #efefef;
  cursor: pointer;
}
