.tab {
  display: inline-block;
  margin: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  color: #666;
  border-radius: 4px;
  padding-bottom: 5px;
  position: relative;
}

.tab:first-child {
  margin-left: 0px;
}

.tab.active {
  color: #888;
  color: #f2f2f2;
}

.tab::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #fff;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.tab.active::after {
  transform: scaleX(1);
}

.models h3 {
  padding: 60px 0 30px 0;
  font-size: 32px;
  font-weight: bold;
}

.models {
  width: 1728px;
  height: auto;
  margin: auto;
  margin-bottom: 100px;
}

.tabMenu {
  padding-bottom: 20px;
}
